<template>
  <div class="add-property pb-2">
    <div class="px-3 back_close d-flex justify-content-between align-items-center py-3 d-md-none">
      <b-icon font-scale="1.5" icon="arrow-left" @click="$router.go(-1)"></b-icon>
      <b-icon icon="x-lg" @click="$router.push(localePath('/'))"></b-icon>
    </div>
    <b-overlay :show="loading" rounded="sm" :opacity="1">
      <div class="post-form-container">
        <!-- <ProgressCircle :current-step="4" :total-steps="5"></ProgressCircle> -->
        <b-progress class="w-100 mt-lg-2" :value="4" :max="5" :height="progressHeight"></b-progress>
        <div class="p-3 step">
          <UserTerms
            @nextStep="goNext"
            :conditionsData="userConditions"
            :propertiesData="propertiesData"/>
        </div>
      </div>
    </b-overlay>
    <WarningModal
      :show="showWarningModal"
      :routeTo="routeTo"
      @closeModal="showWarningModal = false"
      @closeModalAndNavigate="closeModalAndNavigate" />
  </div>
</template>

<script>
import UserTerms from '@/components/postingRequestForm/UserTerms';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';
import WarningModal from '@/components/postingRequestForm/WarningModal';
export default {
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  components: {
    UserTerms,
    ProgressCircle,
    WarningModal,
  },
  data() {
    return {
      currentRequest: null,
      propertiesData: {},
      userConditions: [],
      loading: false,
      showWarningModal: false,
      routeTo: '',
      navigate: false,
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Posting Request | Step 3" : "نشر طلب | الخطوة 3"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Posting Request | Terms Selection' : 'نشر طلب | إختيار الشروط'
        }
      ]
    }
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    appEnv(){
      return this.$config.APP_ENV
    },
    progressHeight(){
      if(process.client){
        if(window.innerWidth <= 991){
          return '3px'
        }else{
          return '5px'
        }
      }
    }
  },
  methods: {
    goNext(values) {
      localStorage.setItem(
        'request',
        JSON.stringify({
          ...this.currentRequest,
          selectPreferences: values,
        })
      );
      this.$router.push(this.localePath('/requests/post/preview'))
    },
    closeModalAndNavigate() {
      this.showWarningModal = false;
      this.navigate = true;
    },
  },
  async mounted() {
    this.currentRequest =
      localStorage.getItem('request') &&
      JSON.parse(localStorage.getItem('request'));

    this.loading = true;
    this.term = localStorage.getItem('request')? JSON.parse(localStorage.getItem('request')).stayingDetails.selectedDurationType === 'days'? 0 : 1
      : 0;

    const { Data } = await this.$axios.$get('/getPropMasterData');
    this.propertiesData = Data;

    const terms = await this.$axios.$get(`/conditions?term=${this.term}`);
    this.userConditions = terms.Data.conditions;
    this.loading = false;
  },
  beforeRouteLeave(to, from, next) {
    if (!to.path.includes('/requests/post/') && this.currentRequest) {
      this.showWarningModal = true;
      this.routeTo = to.path;

      if (this.navigate) return next();
    } else next();
  },
};
</script>

<style lang="scss">
.back_close {
  background-color: #f2f2f2 !important;
}
.add-property {
  overflow: hidden;
}
</style>