<template>
  <!-- headeing -->
  <div class="row user-terms">
    
    <b-col cols="12" lg="6" class="mt-2 step-content pt-2 mx-auto">
      <b-row>
        <b-col cols="12" class="step-heading">
          <h1
            class="second-step__title font-weight-semi font-size-large text-center">
            {{ $t('form.postingRequest.userTerms.heading') }}
          </h1>
        </b-col>
        <!-- UserTerms Checkboxes -->
        <b-col cols="12" class="mt-2 section-white mb-0">
          <b-row>
            <b-col cols="12">
              <h3 class="font-size-semi-large dark-blue title">
                {{ $t('form.postingRequest.preview.your_terms') }}
                <span style="color: red">*</span>
              </h3>
              <template v-if="terms.checkBoxes.length">
                <chip-selector
                  :options="terms.checkBoxes"
                  @update-selection="updateSelectedTerm"
                  label="text"></chip-selector>
              </template>
            </b-col>
            <b-col cols="12">
              <!-- UserTerms RadioBtns -->
              <b-row v-if="terms.radioBtns.length">
                <div class="details-container col-12" v-for="radio in terms.radioBtns" :key="radio.id">
                  <div class="arrive-section">
        
                    <h5 class="font-size-semi-large dark-blue title">
                      {{ radio.name }}
                    </h5>
                    <!-- radio-btn -->
                    <b-row>
                      <b-col v-for="val in radio.input_values" :key="val.value_en + radio.id">
                        <!-- This Assign Value For Testing -->
                        <div :class="['form-check', dir === 'rtl' ? 'forAr' : 'forEn']"> 
                          <input
                            @change="getConditions(radio.id, dir === 'ltr' ? val.value_en : val.value_ar, radio.name)"
                            class="form-check-input"
                            type="radio"
                            :name="replaceSpacesWithunderscore(radio.name)"
                            :id="replaceSpacesWithunderscore(val.value_en)" />
                          <label class="form-check-label font-weight-normal" :for="replaceSpacesWithunderscore(val.value_en)">
                            {{ dir === 'ltr' ? val.value_en : val.value_ar }}
                          </label>
                        </div>
                      </b-col>
                    </b-row>
                  </div>
                </div>
              </b-row>
            </b-col>
          </b-row>
        </b-col>
        <!-- UserTerms Int Inputs -->
        <b-col cols="12">
          <b-row v-if="terms.inputInt.length">
            <b-col cols="12" class="details-container cancellation mt-0 section-white py-2">
              <b-row class="justify-content-between mb-3" v-for="inputTerm in terms.inputInt" :key="inputTerm.id">
                <b-col md="12" lg="6" class="px-1">
                  <h5 class="font-size-semi-large dark-blue title" style="font-size: 13px;">
                    {{ inputTerm.name }}
                  </h5>
                  <p v-if="inputTerm.name.toLowerCase().indexOf('Cancellation'.toLowerCase()) >= 0">You can cancel this reservation after the specific days, after this the price will be Non-refundable.</p>
                </b-col>
    
                <b-col md="12" lg="3">
                  <CustomCounter
                    @inputt="userInputs($event, inputTerm.id, inputTerm.name)"
                    @changee="getConditions(inputTerm.id, parseInt(inputTerm.value), inputTerm.name)"
                    :id="`${inputTerm.name}_${inputTerm.id}`"
                    :step="1"
                    validation-rules="required|min_value:1"
                    v-model="inputTerm.value"
                    :min="1"
                    :max="365" />
                </b-col>
              </b-row>
            </b-col>
          </b-row>
        </b-col>
      </b-row>
      <!-- default condtion -->
      <div class="divider"></div>
      <b-row>
        <b-col cols="12" class="section-white">
          <h5 class="font-size-semi-large dark-blue title">
            <!-- furnishing_title -->
            {{ $t('property.furnishing_status') }}
            <span style="color: red">*</span>
          </h5>
          <!-- furnishing chip selector -->
          <chip-selector
            :options="transformFurnishTypes"
            :defaultValue="selectPreferences.furnishType"
            @update-selection="updateFurnishTypes"></chip-selector>
        </b-col>
      </b-row>
      <!-- default condtion -->
      <div class="divider"></div>
      <b-col cols="12" class="mt-4 section-white">
        <b-row class="justify-content-between">
          <b-col md="12" lg="4">
            <h5 class="font-size-semi-large dark-blue title">
              {{ $t('form.postingRequest.SelectPreferences.minimum_rooms') }}
              <span style="color: red">*</span>
            </h5>
          </b-col>

          <b-col md="12" lg="3">
            <CustomCounter
              id="minimumRooms"
              :step="1"
              validation-rules="required|min_value:1"
              v-model="selectPreferences.miniumRooms"
              :min="1"
              :max="10" />
          </b-col>
        </b-row>
      </b-col>
      <!-- default condtion -->
      <div class="divider"></div>

      <b-col cols="12" class="mt-4 section-white">
          <!-- property_title -->
        <h5 class="font-size-semi-large dark-blue title">
          {{ $t('property.property_types') }}
          <span style="color: red">*</span>
        </h5>
        <!-- property ship selector -->
        <chip-selector
          :options="propertyTypes"
          :defaultValue="selectPreferences.propertyType"
          @update-selection="updatePropertyTypes"></chip-selector>
      </b-col>
    </b-col>
    <template v-if="showNext">
      <!-- buttons container -->
      <b-col cols="12">
        <b-row class="step-next-container mx-auto">
          <b-col cols="5">
            <div>
              <b-button
                @click="
                  () => {
                    $router.push(
                      this.localePath('/requests/post/staying-details')
                    );
                  }
                "
                variant="primary"
                class="back-btn">
                <span>{{ $t('common.back') }}</span>
              </b-button>
            </div>
          </b-col>
          <b-col cols="7">
            <div>
              <b-button
                @click="handleNextClick"
                variant="primary"
                class="next-btn">
                <span>{{ $t('common.next') }}</span>
              </b-button>
            </div>
          </b-col>
        </b-row>
      </b-col>
    </template>
  </div>
</template>

<script>
import CounterInput from '@/components/shared/CounterInput';
import ChipSelector from '~/components/shared/form/chipSelector';
import axios from 'axios'
export default {
  name: 'UserTerms',
  props: {
    showDescription: {
      type: Boolean,
      default: () => true,
    },
    showNext: {
      type: Boolean,
      default: () => true,
    },
    conditionsData: {
      type: Array,
      default: [],
    },
    propertiesData: { type: Object, default: () => {} },
  },
  components: {
    ChipSelector,
    CounterInput,
    CustomCounter: () => import('~/components/shared/form/CustomCounter'),
  },
  data() {
    return {
      selectPreferences: {
        miniumRooms: 1,
        minimumContract: 1,
        evictionNotice: 1,
        maximumAnnualIncrease: null,
        arrive: 'Early check-in',
        furnishType: [],
        propertyType: [],
        userTerms: [],
        cancellationDays: 5
      },
      conditions: [],
      conditions_for_Preview: [],
      propertyTypes: [],
      terms: {
        checkBoxes: [],
        radioBtns: [],
        inputInt: [],
      },
      propertyCategory: {},
    };
  },
  computed: {
    transformUserConditions() {
      return this.conditionsData.map((condition) => ({
        value: condition.id,
        text: condition[`name_${this.$i18n.locale}`],
      }));
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    transformFurnishTypes() {
      return (
        this.propertiesData.furnishType &&
        this.propertiesData.furnishType.map((furnish) => ({
          value: furnish.id,
          label: furnish.text,
        }))
      );
    },
    transformPropertyTypes() {
      return (
        this.propertiesData.propertyType &&
        this.propertiesData.propertyType.map((property) => ({
          value: property.prop_type_id,
          label: property.prop_type_name,
        }))
      );
    },
  },
  methods: {
    updateSelectedTerm(terms) {
      this.selectPreferences.userTerms = terms;
      this.conditions = this.conditions.filter(condition => typeof(condition.value) !== 'boolean');
      this.conditions_for_Preview = this.conditions_for_Preview.filter(condition => typeof(condition.value) !== 'boolean');
      for(let term of terms){
        this.getConditions(term, true, "");
      }

    },
    updatePropertyTypes(values) {
      this.selectPreferences.propertyType = values;
    },
    updateFurnishTypes(values) {
      this.selectPreferences.furnishType = values;
    },
    handleNextClick() {
      localStorage.setItem('conditions', JSON.stringify(this.conditions))
      localStorage.setItem('conditionsPreview', JSON.stringify(this.conditions_for_Preview))
      let { furnishType, miniumRooms, propertyType, userTerms } =
        this.selectPreferences;

        if (!this.conditions.length) {
        window.scrollTo({
          top: 120,
          left: 0,
          behavior: 'smooth',
        });
        let body = this.$t('form.one_condition_at_Least');
        this.$bvToast.toast(body, {
          title: this.$t('form.fillInput'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }
      
      
        if (!furnishType || !propertyType) {
        window.scrollTo({
          top: 120,
          left: 0,
          behavior: 'smooth',
        });
        let body = this.$t('form.checkListFirst');
        this.$bvToast.toast(body, {
          title: this.$t('form.fillInput'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }
      if (
        !furnishType.length ||
        !miniumRooms ||
        !propertyType.length
      ) {
        window.scrollTo({
          top: 120,
          left: 0,
          behavior: 'smooth',
        });
        let body = this.$t('form.checkListFirst');
        this.$bvToast.toast(body, {
          title: this.$t('form.fillInput'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 1000,
        });
        return;
      }

      this.$emit('nextStep', this.selectPreferences);
    },
    replaceSpacesWithunderscore(text){
      return text.replaceAll(/\s/g, '_');
    },
    getConditions(conditionId, val, name){
      if(this.conditions.length){
        if(val !== null || val.trim() !== ''){
          for(let condition of this.conditions){
            if(condition.condition_id === conditionId){
              condition.value = val;
              let currentConditionInPreview = this.conditions_for_Preview.filter(condition => condition.id === conditionId)[0];
              currentConditionInPreview.value = val;
              return;
            }
          }
        }else{
          return;
        }
        this.conditions.push({ condition_id: conditionId, value: val });
        this.conditions_for_Preview.push({ id: conditionId, value: val, name: name });
      }else{
        this.conditions.push({ condition_id: conditionId, value: val });
        this.conditions_for_Preview.push({ id: conditionId, value: val, name: name });
      }
    },
    userInputs(e, id, name){
      this.getConditions(id, parseInt(e), name)
    }
  },
  async mounted() {
    this.conditions = [];
    this.conditions_for_Preview = [];
    const request = localStorage.getItem('request') ? JSON.parse(localStorage.getItem('request')) : this.$router.push(this.localePath('/requests/post/property-category'));
    
    let selectedCategory = request.propertyCategory;
    this.propertyCategory = request.propertyCategory;

    const propertyRes = await axios.get(`https://apibeta.rentup.co/api/tags/${selectedCategory.id}/propertyTypeByTag`);
    const conditionRes = await axios.get(`https://apibeta.rentup.co/api/tags/${selectedCategory.id}/conditionByTag`);

    this.propertyTypes = [];
    for(let property of propertyRes.data.Data){
      this.propertyTypes.push({value: property.id, label: property.name})
    }

    let allConditions = conditionRes.data.Data;

    this.terms.checkBoxes = [];
    this.terms.radioBtns = [];
    this.terms.inputInt = [];

    let checkBoxes = allConditions.filter(condition => condition.category_name.indexOf('Check') >= 0);
    if(checkBoxes.length){
      for(let term of checkBoxes){
        this.terms.checkBoxes.push({value: term.id, text: term.name})
      }
    }

    this.terms.radioBtns = allConditions.filter(condition => condition.category_name.indexOf('Radio') >= 0);
    this.terms.inputInt = allConditions.filter(condition => condition.category_name.indexOf('Input') >= 0);


    for(let termInput of this.terms.inputInt){
      termInput.value = 1
    }

    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });

    this.selectPreferences.userTerms = request.selectPreferences?.userTerms;
    this.selectPreferences.furnishType = request.selectPreferences?.furnishType;
    this.selectPreferences.propertyType =
      request.selectPreferences?.propertyType;
    this.selectPreferences.miniumRooms =
      request.selectPreferences?.miniumRooms || 1;
  },
};
</script>

<style lang="scss" scoped>
.custom-terms-container {
  .term-container {
    display: flex;
    justify-content: space-between;
    h5 {
      margin: auto 0;
    }
    .percentage-sign {
      position: absolute;
      right: 50px;
      top: 25px;
      user-select: none;
    }
  }
  ::v-deep() .counter-input-contianer {
    gap: 5px;
  }
  ::v-deep() .input-field {
    width: 100px !important;
  }
}

.details-container {
  background: #ffffff;
  padding: 15px 25px 20px;
  overflow: hidden;
  gap: 20px;
  .form-check {
    margin-bottom: 10px;
    margin-top: 10px;
    display: flex;
    align-items: center;
    padding-left: 0;
    &.forEn {
      gap: 10px;
      .form-check-input {
        margin-left: 0;
      }
      & .form-check-label {
        font-size: 13px;
      }
    }
    &.forAr {
      gap: 25px;
      .form-check-input {
        margin-right: 0;
      }
      & .form-check-label {
        font-size: 15px;
      }
    }
    .form-check-input {
      position: static;
      width: 20px;
      height: 20px;
      margin-top: auto;
    }
    .form-check-label {
      font-weight: 600;
      line-height: 14px;
      text-align: center;
      cursor:pointer;
    }
  }

  .cancellation {
    gap: 20px;
    p {
      padding-right: 20px;
      font-size: 12px;
    }
  }
  .prop-type {
    border: 0 !important;
  }
  .title-section {
    margin-bottom: 15px;
  }
  .row {
    gap: 5px;
  }
  .details-item {
    width: calc((100% / 2) - 10px);
    text-align: center;
    padding: 15px;
    border: solid 1px #e3e5e5;
    border-radius: 10px;
    h3 {
      text-align: center;
      margin: 15px 0 0;
      color: black;
      font-weight: bold;
    }
    span {
      font-size: 12px;
      color: #48535b;
    }
  }
}
.user-terms {
  .title {
    font-weight: 600;
    font-size: 16px;
    line-height: 16px;
    color: #111111;
  }
  .row {
    align-items: center;
  }
}
.step-content {
  .divider {
    width: 120%;
    height: 5px;
    position: relative;
    left: -30px;
    background-color: #f7f9fa;
  }
}

.user-terms .custom-control.custom-checkbox {
  width: 100%;

  margin-bottom: 10px;
  padding: 10px;
  border-radius: 4px;
  background: linear-gradient(
      0deg,
      rgba(38, 45, 61, 0.04),
      rgba(38, 45, 61, 0.04)
    ),
    #ffffff;

  span: {
    color: #0f256e !important;
    font-weight: bold;
    margin-left: 1.5rem;
    margin-bottom: 0 !important;
  }
}
.annual-increase {
  border: solid 1px #e3e5e5;
  padding: 10px;
  border-radius: 10px;
}
.contract-section {
  .row:first-of-type {
    border-bottom: 1px solid #f2f4f5;
  }
  .row {
    padding: 15px 0px;
  }
  .row:last-of-type {
    border-top: 1px solid #f2f4f5;
  }
}
.user-terms .custom-control-label span {
  color: #0f256e !important;
  font-weight: bold;
  margin-left: 1.5rem;
  margin-bottom: 0 !important;

  [dir='rtl'] & {
    margin-left: unset;
    margin-right: 1.5rem !important;
  }
}
.furnish-radio {
  display: grid;
  .custom-radio {
    margin: 10px 0px;
    padding: 0px;
    &:first-child {
      padding-bottom: 20px;
      border-bottom: 1px solid #f2f4f5;
    }
    .custom-control-label {
      &::after {
        left: 0px;
      }
    }
  }
}
.user-terms {
  .form-group {
    .custom-control {
      .custom-control-label {
        &::before {
          left: -24px;
        }
      }
    }
  }
}
.user-terms .custom-control-label::before {
  left: 0;

  [dir='rtl'] & {
    left: auto;
    right: 0 !important;
  }
}
</style>