<template>
  <div class='progress-circle-container'>
    <div class="percent">
      <svg>
        <circle cx="30" cy="30" r="25"></circle>
        <circle cx="30" cy="30" r="25" v-bind:style="`--percent: ${currentStep/totalSteps * 100}`"></circle>
      </svg>
      <div class="number">
        <h3>{{currentStep}}  {{ dir === 'rtl' ? 'من' : 'of' }} {{totalSteps}}</h3>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ProgressCircle',
  props: {
    currentStep: {
      type: Number,
      default: () => 0
    },
    totalSteps: {
      type: Number,
      default: () => 1
    }
  },
  computed:{
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr'
    },
  }
}
</script>

<style scoped>


.progress-circle-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 60px;
  height: 60px;
  border-radius: 5px;
  margin: auto;
  margin-top: 10px;
}
.progress-circle-container .percent {
  position: relative;
}

.progress-circle-container svg {
  position: relative;
  width: 60px;
  height: 60px;
  transform: rotate(-90deg);
}

.progress-circle-container svg circle {
  width: 100%;
  height: 100%;
  fill: none;
  stroke: rgba(0, 120, 255, 0.1);
  stroke-width: 5;
  stroke-linecap: round;
}

.progress-circle-container svg circle:last-of-type {
  stroke-dasharray:125px;
  stroke-dashoffset: calc(125px - (125px * var(--percent)) / 100);
  stroke: #3498db;
}

.progress-circle-container .number {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.progress-circle-container .number h3 {
  font-weight: 200;
  font-size: .6rem;
  color: #0078FF;
}

.progress-circle-container .number h3 span {
  font-size: 2rem;
}

.progress-circle-container .title h2 {
  margin: 25px 0 0;
}

.progress-circle-container:nth-child(1) svg circle:last-of-type {
  stroke: #0078FF;
}
</style>