<template>
  <b-modal v-model="toggleModel">
    <template #modal-header> <b>{{ $t('common.warning') }}</b> </template>

    {{ $t('common.warning_message') }}

    <template #modal-footer>
      <div class="w-100">
        <b-button
          variant="danger"
          size="sm"
          class="float-right"
          @click="removeCache">
          {{ $t('common.discard_ch') }}
        </b-button>
        <b-button
          variant="success"
          size="sm"
          class="float-right mx-3"
          @click="closeModalAndNavigate">
          {{ $t('common.save_for_later') }}
        </b-button>
      </div>
    </template>
  </b-modal>
</template>

<script>
export default {
  props: {
    show: Boolean,
    routeTo: String,
  },
  computed: {
    toggleModel: {
      get: function () {
        return this.show;
      },
      set: function () {
        this.$emit('closeModal');
      },
    },
  },
  methods: {
    removeCache() {
      localStorage.removeItem('request');
      this.closeModalAndNavigate();
    },
    closeModalAndNavigate() {
      this.$emit('closeModalAndNavigate');
      this.$router.push(this.routeTo);
    },
  },
};
</script>

<style></style>
